.beers__list__favorite__container {
  width: 70%;
  max-width: 860px;
  margin: 0 auto;
  font-family: 'Lato', serif;
}
.beers__list__favorite__container .beer__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-height: 80px;
  border-bottom: 1px solid #7942ED;
  margin-bottom: 5px;
}
.beers__list__favorite__container .beer__list .beer_list__info {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.beers__list__favorite__container .beer__list .beer_list__info .info {
  width: 50%;
}
.beers__list__favorite__container .beer__list .beer_list__info .beer__image {
  display: flex;
  align-items: center;
}
.beers__list__favorite__container .beer__list .beer_list__info .beer__image img {
  max-height: 70px;
}
