@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Typography */
/* Font weigth */
/* Colors */
.spinner__container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #51495e;
}

/* Typography */
/* Font weigth */
/* Colors */
.header__container {
  margin-bottom: 20px;
  font-family: 'Lato', serif;
}
.header__container .user__options {
  display: flex;
  align-items: center;
  margin: 20px 0 20px 0;
}
.header__container .user__options .logo {
  font-weight: 900;
  cursor: pointer;
}
.header__container .user__options .logo img {
  width: 25%;
}
@media only screen and (max-width: 548px) {
  .header__container .user__options .logo img {
    width: 60%;
  }
}
.header__container .disabled {
  display: none;
}
.header__container .search__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: 'Lato', serif;
}
.header__container .search__container form {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form {
    flex-direction: column;
    align-items: center;
  }
}
.header__container .search__container form input[type=text] {
  max-width: 25%;
  border: 2px solid #ABA4BA;
  border-radius: 4px;
  width: 100%;
  padding: 15px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  margin-top: 0px;
  margin-right: 10px;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form input[type=text] {
    max-width: 90%;
    margin-bottom: 5px;
  }
}
.header__container .search__container form input[type=reset] {
  padding: 0.92857rem 1.57143rem;
  border-radius: 999rem;
  bottom: 0;
  font-size: 1.07143rem;
  background-color: #0ececf;
  color: white;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form input[type=reset] {
    width: 90%;
  }
}
.header__container .disabled {
  display: none;
}

/* Typography */
/* Font weigth */
/* Colors */
.selected_beer__container {
  width: 70%;
  max-width: 860px;
  margin: 0 auto;
  font-family: 'Lato', serif;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container {
    width: 90%;
  }
}
.selected_beer__container .selected_beer_content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content {
    flex-direction: column;
  }
}
.selected_beer__container .selected_beer_content .separator {
  width: 80%;
  margin: 0 auto;
  border-bottom: 2px solid #7942ED;
}
.selected_beer__container .selected_beer_content .selected_beer_info {
  max-width: 50%;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content .selected_beer_info {
    max-width: 100%;
  }
}
.selected_beer__container .selected_beer_content .selected_beer_info h1 {
  font-size: 35px;
  font-weight: 900;
}
.selected_beer__container .selected_beer_content .selected_beer_info h3 {
  font-size: 20px;
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_info .description {
  margin-top: 20px;
}
.selected_beer__container .selected_beer_content .selected_beer_info .ingredients .sub__ingredients p {
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_info .food p {
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content .selected_beer_image {
    margin-top: 15px;
  }
}
.selected_beer__container .selected_beer_content .selected_beer_image img {
  width: 50%;
}

/* Typography */
/* Font weigth */
/* Colors */
.beers__list {
  width: 80%;
  margin: 0 auto;
}
.beers__list .card_beer_content {
  font-family: 'Lato', serif;
  min-height: 380px;
  max-width: 500px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  border: 0.5px solid #51495e;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.beers__list .card_beer_content .card_beer__header {
  font-family: 'Lato', serif !important;
  border-bottom: 1px solid #51495e;
}
.beers__list .card_beer_content .card_beer__header .MuiCardHeader-content .MuiCardHeader-title {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 80%;
  font-weight: 700;
}
.beers__list .card_beer_content .card_beer__avatar {
  background-color: #51495e;
}
.beers__list .card_beer_content .card_beer__media {
  height: 0;
  padding-top: 56.25%;
  background-position: center;
  background-size: contain;
}
.beers__list .pagination__content {
  padding: 20px;
  display: flex;
  justify-content: center;
}

/* Typography */
/* Font weigth */
/* Colors */
.beers__list__container {
  font-family: 'Lato', serif;
}
.beers__list__container .beer__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-height: 80px;
  border-bottom: 1px solid #7942ED;
  margin-bottom: 5px;
}
@media only screen and (max-width: 548px) {
  .beers__list__container .beer__list {
    max-height: none;
  }
}
.beers__list__container .beer__list .beer_list__info {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.beers__list__container .beer__list .beer_list__info .info {
  width: 50%;
}
.beers__list__container .beer__list .beer_list__info .beer__image {
  display: flex;
  align-items: center;
}
.beers__list__container .beer__list .beer_list__info .beer__image img {
  max-height: 70px;
}

/* Typography */
/* Font weigth */
/* Colors */
.beers__list__favorite__container {
  width: 70%;
  max-width: 860px;
  margin: 0 auto;
  font-family: 'Lato', serif;
}
.beers__list__favorite__container .beer__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-height: 80px;
  border-bottom: 1px solid #7942ED;
  margin-bottom: 5px;
}
.beers__list__favorite__container .beer__list .beer_list__info {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.beers__list__favorite__container .beer__list .beer_list__info .info {
  width: 50%;
}
.beers__list__favorite__container .beer__list .beer_list__info .beer__image {
  display: flex;
  align-items: center;
}
.beers__list__favorite__container .beer__list .beer_list__info .beer__image img {
  max-height: 70px;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
html #root,
body #root {
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

