/* Colors */
.beers__list {
  width: 80%;
  margin: 0 auto;
}
.beers__list .card_beer_content {
  font-family: 'Lato', serif;
  min-height: 380px;
  max-width: 500px;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  border: 0.5px solid #51495e;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.beers__list .card_beer_content .card_beer__header {
  font-family: 'Lato', serif !important;
  border-bottom: 1px solid #51495e;
}
.beers__list .card_beer_content .card_beer__header .MuiCardHeader-content .MuiCardHeader-title {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  width: 80%;
  font-weight: 700;
}
.beers__list .card_beer_content .card_beer__avatar {
  background-color: #51495e;
}
.beers__list .card_beer_content .card_beer__media {
  height: 0;
  padding-top: 56.25%;
  background-position: center;
  background-size: contain;
}
.beers__list .pagination__content {
  padding: 20px;
  display: flex;
  justify-content: center;
}
