/* Colors */
.header__container {
  margin-bottom: 20px;
  font-family: 'Lato', serif;
}
.header__container .user__options {
  display: flex;
  align-items: center;
  margin: 20px 0 20px 0;
}
.header__container .user__options .logo {
  font-weight: 900;
  cursor: pointer;
}
.header__container .user__options .logo img {
  width: 25%;
}
@media only screen and (max-width: 548px) {
  .header__container .user__options .logo img {
    width: 60%;
  }
}
.header__container .disabled {
  display: none;
}
.header__container .search__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: 'Lato', serif;
}
.header__container .search__container form {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form {
    flex-direction: column;
    align-items: center;
  }
}
.header__container .search__container form input[type=text] {
  max-width: 25%;
  border: 2px solid #ABA4BA;
  border-radius: 4px;
  width: 100%;
  padding: 15px;
  appearance: none;
  outline: none;
  margin-top: 0px;
  margin-right: 10px;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form input[type=text] {
    max-width: 90%;
    margin-bottom: 5px;
  }
}
.header__container .search__container form input[type=reset] {
  padding: 0.92857rem 1.57143rem;
  border-radius: 999rem;
  bottom: 0;
  font-size: 1.07143rem;
  background-color: #0ececf;
  color: white;
}
@media only screen and (max-width: 548px) {
  .header__container .search__container form input[type=reset] {
    width: 90%;
  }
}
.header__container .disabled {
  display: none;
}
