/* Font weigth */
/* Colors */
.selected_beer__container {
  width: 70%;
  max-width: 860px;
  margin: 0 auto;
  font-family: 'Lato', serif;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container {
    width: 90%;
  }
}
.selected_beer__container .selected_beer_content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content {
    flex-direction: column;
  }
}
.selected_beer__container .selected_beer_content .separator {
  width: 80%;
  margin: 0 auto;
  border-bottom: 2px solid #7942ED;
}
.selected_beer__container .selected_beer_content .selected_beer_info {
  max-width: 50%;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content .selected_beer_info {
    max-width: 100%;
  }
}
.selected_beer__container .selected_beer_content .selected_beer_info h1 {
  font-size: 35px;
  font-weight: 900;
}
.selected_beer__container .selected_beer_content .selected_beer_info h3 {
  font-size: 20px;
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_info .description {
  margin-top: 20px;
}
.selected_beer__container .selected_beer_content .selected_beer_info .ingredients .sub__ingredients p {
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_info .food p {
  font-weight: 700;
}
.selected_beer__container .selected_beer_content .selected_beer_image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 548px) {
  .selected_beer__container .selected_beer_content .selected_beer_image {
    margin-top: 15px;
  }
}
.selected_beer__container .selected_beer_content .selected_beer_image img {
  width: 50%;
}
